export default{
	data(){
		return{
			changepassword:''
		};
	},
	onLoad() {
},
	methods:{
	changePassword:function(changepassword){
		var _this = this;
		uni.request({
		url: 'http://localhost:8080/api/user/password?id='+uni.getStorageSync('login_key').userId,
		method:'put',
		data:changepassword,
		header: { 'content-type':'application/json' },
			success: res => {
				uni.navigateBack();
				}
		});
	}	
	}
}
